<template>
<div id="reports">
    <div class="row">
        <div class="col-12">
            <h1 class="fw-bold title-page">Báo cáo tổng hợp</h1>
        </div>
    </div>
    
    <div class="row mb-2">
        <div class="col-6">
            <div class="form-group">
                <label for="">Loại báo cáo</label>
                <select2 v-model="searchBy.reportType" :options="reportTypes" :settings="settings" />
            </div>
        </div>
        <div class="col-3">
            <div class="form-group">
                <label for="">Tháng</label>
                <select v-model="searchBy.currentMonth" name="" id="" class="form-select">
                    <option value="0">--Chọn tháng--</option>
                    <option v-for="item in months" :key="item">
                    <option :value="item">{{ item }}</option>
                    </option>
                </select>
            </div>
        </div>

        <div class="col-3">
            <div class="form-group">
                <label for="">Năm</label>
                <select v-model="searchBy.currentYear" name="" id="" class="form-select">
                    <option value="0">--Chọn năm--</option>
                    <option value="2024">2024</option>
                    <option value="2023">2023</option>
                    <option value="2022">2022</option>
                </select>
            </div>
        </div>
    </div>

    <div class="row mb-2 collapse" id="collapseExample">
        <div class="col-3">
            <div class="form-group">
                <label for="">Mã thuê bao</label>
                <input type="text" v-model="searchBy.ma_tb" class="form-control" placeholder="Tìm theo mã thuê bao">
            </div>
        </div>
        <div class="col-3">
            <div class="form-group">
                <label for="">Mã lắp đặt</label>
                <input type="text" v-model="searchBy.contract_code" class="form-control" placeholder="Tìm theo mã lắp đặt">
            </div>
        </div>
        <div class="col-3">
            <div class="form-group">
                <label for="">Đầu mối liên hệ</label>
                <select2 v-model="searchBy.customer_id" :options="customers" :settings="settings" />

            </div>
        </div>
    </div>
    <div class="row">
        <div class="col-12">
            <button class="btn btn-primary-soft me-1" @click="searchReport()"><i class="fa-thin fa-magnifying-glass"></i> Tìm kiếm</button>
            <button class="btn btn-primary-soft me-1" data-bs-toggle="collapse" href="#collapseExample" role="button" aria-expanded="false" aria-controls="collapseExample"><i class="fa-thin fa-filter-list"></i> Nâng cao</button>
            <button class="btn btn-success" @click="exportExcel"><i class="fa-thin fa-file-excel"></i> Xuất Excel</button>
        </div>
    </div>
    <div class="row">
        <div class="col-12">
            <table class="table table-responsive">
                <thead>
                    <tr>
                        <th>#</th>
                        <th width="30%">Tên công ty</th>
                        <th>Mã số thuế</th>
                        <th>Ngày HĐ</th>
                        <th>Hình thức</th>
                        <th>Ngày xuất HĐ</th>
                        <th width="4%">HT</th>
                        <th width="7%">Thanh toán</th>
                        <th>Tổng tiền</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td></td>
                        <td><input v-model="searchBy.company_name" type="text" class="form-control" placeholder="Tìm tên công ty" /></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr v-for="(item,index) in reportFilterd" :key="item.contract_id">
                        <td>{{ index+1}}</td>
                        <td>
                            <router-link :title="item.company_name" :to="{
                  name: 'contract-edit',
                  params: { id: item.contract_id },
                }">
                                <span class="text-capitalize">{{ item.company_name }}</span>
                            </router-link>
                            <div v-if="item.contract_note">
                              <i class="fa-regular fa-notes me-1"></i>
                              <i class="text-danger fw-bold">{{ item.contract_note }}</i>
                            </div>
                        </td>
                        <td>{{ item.company_taxcode }}</td>
                        <td>{{ item.contract_date | moment("DD/MM/YYYY") }}</td>
                        <td v-html="contractType(item.contract_type)">
                        </td>
                        <td>{{ item.complete_date | moment("DD/MM/YYYY") }}</td>
                        <td v-html="contractStatus(item.contract_status)"> </td>

                        <template v-if="item.contract_payment == 4 && item.contract_status ==3">

                            <td><span class="text-small">{{ item.contract_payment_date |moment('DD/MM/YYYY H:mm:ss') }}</span></td>
                        </template>
                        <template v-else>
                            <td v-html="contractPayment(item.contract_payment)"></td>
                        </template>
                        <td class="text-end"><strong class="text-danger">{{ item.contract_total | formatamout(0)}}</strong></td>
                        <td>

                            <div class="dropdown">
                                <button class="btn btn-sm border-0" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                                    <i class="fa-solid fa-ellipsis-vertical"></i>
                                </button>
                                <ul class="dropdown-menu">
                                    <li>
                                        <router-link class="dropdown-item hand" :to="{
                        name: 'contract-detail',
                        params: {
                          id: item.contract_id,
                        },
                      }">
                                            <i class="fa-thin fa-circle-info"></i> Xem chi tiết
                                        </router-link>
                                    </li>
                                    <li>
                                        <router-link class="dropdown-item" :to="{
                        name: 'contract-edit',
                        params: { id: item.contract_id },
                      }">
                                            <i class="fa-thin fa-pen-to-square"></i> Cập nhật hợp đồng
                                        </router-link>
                                    </li>
                                    <li v-if="item.contract_status < 3">
                                        <a class="dropdown-item hand" @click="completeContract(item, 0)"><i class="fa-thin fa-circle-check"></i> Hoàn thiện hợp đồng</a>
                                    </li>
                                    <li v-if="item.contract_payment < 3">
                                        <a class="dropdown-item hand" @click="completeContract(item, 1)"><i class="fa-thin fa-circle-dollar"></i> Thanh toán hợp đồng</a>
                                    </li>
                                    <li>
                                        <router-link class="dropdown-item hand" title="Tùy chỉnh hợp đồng" :to="{
                      name:'contract-option',
                      params:{
                        id: item.contract_id
                      }
                    }">
                                            <i class="fa-thin fa-gear"></i> Tùy chỉnh hợp đồng
                                        </router-link>
                                    </li>

                                    <li>
                                        <hr class="dropdown-divider" />
                                    </li>
                                    <li>
                                        <a class="dropdown-item hand" @click="cancelContract(item, index)">
                                            <strong class="text-danger"><i class="fa-thin fa-trash-can"></i> Hủy hợp đồng</strong></a>
                                    </li>
                                </ul>
                            </div>
                        </td>

                    </tr>
                </tbody>
            </table>

            <not-found v-if="reports.length == 0" />

            <completeContract ref="completeContract" @updateCompleteContract="updateCompleteContract"></completeContract>
        </div>
    </div>
</div>
</template>

<script>
import axios from "axios";
import Select2 from "v-select2-component";
import notFound from "@/extends/NotFound.vue"
import completeContract from "../../extends/ModalCompleteContract.vue";
export default {
    components: {
        Select2,
        notFound,
        completeContract
    },
    data() {
        return {
            reports: [],
            searchBy: {
                reportType: 0,
                currentMonth: new Date().getMonth() + 1,
                currentYear: new Date().getFullYear(),
                ma_tb: "",
                company_name: "",
                customer_id:0,
            },
            reportTypes: [{
                    id: 1,
                    text: "Tất cả hợp đồng trong tháng"
                },
                {
                    id: 2,
                    text: "Phát triển mới trong tháng"
                },
                {
                    id: 3,
                    text: "Hợp đồng gia hạn trong tháng"
                },
                {
                    id: 4,
                    text: "Hợp đồng chưa hoàn thiện & chưa xuất hóa đơn trong tháng"
                },
                {
                    id: 5,
                    text: "Hợp đồng đã hoàn thiện & xuất hóa đơn trong tháng"
                },
                {
                    id: 6,
                    text: "Hợp đồng đã hủy"
                }
            ],
            settings: {
                placeholder: "--Hãy chọn dữ liệu--",
                theme: "bootstrap-5",
                width: "100%",
            },
            months: 12,
            currentMonth: new Date().getMonth() + 1,
            currentYear: new Date().getFullYear(),
            customers : [],
        };
    },
    created() {
       this.getCustomers();
    },
    computed: {
        reportFilterd() {
            return this.reports.filter((x) => {
                return x.company_name.toLowerCase().indexOf(this.searchBy.company_name.toLowerCase()) !== -1;
            })
        }
    },
    methods: {
        async getCustomers(){
            await axios.get('customers').then(response=>{
                this.customers = response.data.map((c)=>{
                    return {
                        id : c.customer_id,
                        text: c.customer_name
                    }
                });
            })
        },
        async searchReport() {
            this.getReports();
        },
        async getReports() {
            this.$econtract.showLoading();
            await axios.post("reports/search", {
                ReportType: {
                    type: this.searchBy.reportType,
                    month: this.searchBy.currentMonth,
                    year: this.searchBy.currentYear
                },
                matb: this.searchBy.ma_tb,
                contractCode: this.searchBy.contractCode,
                customer : this.searchBy.customer_id
            }).then((response) => {
                this.reports = response.data;
                this.$econtract.hideLoading();
            });
        },
        async exportExcel() {

            this.$econtract.showLoading();
            await axios.post('reports', {
                ReportType: {
                    type: this.searchBy.reportType,
                    month: this.searchBy.currentMonth,
                    year: this.searchBy.currentYear
                },
                matb: this.searchBy.ma_tb,
                contractCode: this.searchBy.contractCode
            }, {
                responseType: "blob",
            }).then(response => {
                const url = window.URL.createObjectURL(new Blob([response.data]));
                const link = document.createElement('a');
                link.href = url;
                link.setAttribute('download', 'reports.xlsx'); // Tên file tải về
                document.body.appendChild(link);
                link.click();

                this.$econtract.hideLoading();
            })
        },
        updateCompleteContract(value) {
            let result = this.contracts.filter((item) => {
                return item.contract_id == value.contract_id;
            })[0];
            result.contract_status = value.contract_status;
            result.contract_payment = value.contract_payment;
        },

        completeContract(contract, type) {
            this.$refs.completeContract.show(contract, type);
        },
        cancelContract(item, index) {
            this.$emit("cancelContract", item, index);
        },
        cancelContract(item, index) {
            this.$emit("cancelContract", item, index);
        },
        contractType(type) {
            if (type == 0)
                return ` <span class="badge text-bg-success bg-opacity-10 text-black">
                  Đăng ký mới
                </span>`;
            else
                return ` <span class="badge text-bg-warning bg-opacity-10 text-black">
                  Gia hạn
                </span>`;
        },
        contractStatus(type) {
            if (type == 0)
                return ` <span class="badge text-bg-danger bg-opacity-10 text-black">
                  Mới tạo lập
                </span>`;
            else if (type == 1)
                return ` <span class="badge text-bg-danger bg-opacity-10 text-black">
                  Trình ký
                </span>`;
            else if (type == 2)
                return ` <span class="badge text-bg-danger bg-opacity-10 text-black">
                  Đã gửi KH
                </span>`;
            else if (type == 3)
                return ` <span class="badge text-bg-success bg-opacity-10 text-black">
                  Hoàn thiện
                </span>`;
            else
                return ` <span class="badge text-bg-success bg-opacity-10 text-black">
                  Đã làm thanh toán
                </span>`;
        },
        contractPayment(type) {
            if (type == 0)
                return ` <span class="badge text-bg-danger bg-opacity-10 text-black">
                  Chưa thanh toán
                </span>`;
            else if (type == 1)
                return ` <span class="badge text-bg-danger bg-opacity-10 text-black">
                  Đã nhận tiền nhưng chưa nộp tiền
                </span>`;
            else if (type == 2)
                return ` <span class="badge text-bg-warning text-wrap bg-opacity-10 text-black" style="width:6rem">
                  Đã xuất hóa đơn nhưng chưa nộp tiền
                </span>`;
            else
                return ` <span class="badge text-bg-success bg-opacity-10 text-wrap text-black" style="width:6rem">
                   Đã nộp tiền và xuất hóa đơn
                </span>`;
        }
    },
};
</script>
